import { Input, Component, OnInit, ViewChild } from '@angular/core';
import { Account } from '../../../../blocks/InterFAX/Models/admin';
import { Country } from '../../../../blocks/InterFAX/Models/outbound';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';

@Component({
  selector: 'app-address-tab',
  templateUrl: './address-tab.component.html',
  styleUrls: ['./address-tab.component.scss'],
})
export class AddressTabComponent implements OnInit {
  @Input() account: Account;
  @Input() countries: Array<Country>;

  constructor() {}

  ngOnInit(): void {}

  onValueChange(value: boolean) {
    if (value == true && !this.account.shipping.country) this.account.shipping.country = this.account.billing.country;
  }
}
