import { Component, OnInit, Input } from '@angular/core';
import { Account, AuthenticationTypes } from '../../../../blocks/InterFAX/Models/admin';
import { TranslateService } from '@ngx-translate/core';

import {
  FieldDefinition,
  IFieldOptions,
  FixedDataSource,
  TenantAuthenticationType,
  MetaDataType,
} from 'shared-code-lib';
import { SAMLService } from '../../../../blocks/services/saml-service';
import { InterFAXService } from '../../../../blocks/services/interfax.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-authentication-tab',
  templateUrl: './authentication-tab.component.html',
  styleUrls: ['./authentication-tab.component.scss'],
})
export class AuthenticationTabComponent implements OnInit {
  @Input() account: Account;

  certificateFile: File;

  fieldType: FieldDefinition;
  AuthenticationTypes = AuthenticationTypes;

  fieldClientId: FieldDefinition;
  fieldAuthority: FieldDefinition;

  authenticationTypesDataSource: FixedDataSource;
  MetaDataType = MetaDataType;

  classicLoginPrimaryUsersCount: number = 0;

  get accountAuthenticationOptionsType() {
    return `${this.account.authenticationOptions.type}`;
  }
  set accountAuthenticationOptionsType(val: string) {
    this.account.authenticationOptions.type = Number(val);
  }

  constructor(
    private translateService: TranslateService,
    private samlService: SAMLService,
    private interFAXService: InterFAXService,
    private toastrService: ToastrService,
  ) { }

  ngOnInit(): void {
    this.createFields();
    this.interFAXService.countClassicLoginPrimaryUsers(this.countSuccess, this.countFailed);
  }

  countSuccess = (count: number): void => {
    this.classicLoginPrimaryUsersCount = count;
  };

  countFailed = (err: any): void => {
    this.toastrService.error(err.message);
  };

  createFields = (): void => {
    this.fieldClientId = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-tenant-options.sso-client-id'),
      placeholder: this.translateService.instant('admin-tenant-options.sso-client-id-placeholder'),
      required: true,
    });

    this.fieldAuthority = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-tenant-options.sso-authority'),
      placeholder: this.translateService.instant('admin-tenant-options.sso-authority-placeholder'),
      required: true,
    });

    const authenticationTypes = new Array<IFieldOptions>();
    authenticationTypes.push({
      name: this.translateService.instant('admin-tenant-options.authentication-type-application-based'),
      value: `${TenantAuthenticationType.ApplicationBased}`,
    });
    authenticationTypes.push({
      name: this.translateService.instant('admin-tenant-options.authentication-type-sso'),
      value: `${TenantAuthenticationType.SSO}`,
    });
    authenticationTypes.push({
      name: this.translateService.instant('admin-tenant-options.authentication-type-saml'),
      value: `${TenantAuthenticationType.SAML}`,
    });

    this.authenticationTypesDataSource = new FixedDataSource(authenticationTypes);

    this.fieldType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-tenant-options.authentication-type'),
      placeholder: this.translateService.instant('admin-tenant-options.authentication-type'),
      required: true,
      datasource: this.authenticationTypesDataSource,
    });
  };
}
