import { Input, Component, OnInit } from '@angular/core';
import { Account, FieldState } from '../../../../blocks/InterFAX/Models/admin';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';

@Component({
  selector: 'app-general-tab',
  templateUrl: './general-tab.component.html',
  styleUrls: ['./general-tab.component.scss'],
})
export class GeneralTabComponent implements OnInit {
  @Input() account: Account;

  fieldCompany: FieldDefinition;
  fieldName: FieldDefinition;
  fieldPhone: FieldDefinition;
  fieldFax: FieldDefinition;
  fieldMobile: FieldDefinition;
  fieldEmail: FieldDefinition;
  fieldTaxId: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  private createFields = () => {
    this.fieldCompany = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.company'),
      placeholder: '',
      required: false,
    });

    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.name'),
      placeholder: '',
      required: true,
    });

    this.fieldEmail = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.email'),
      placeholder: '',
      required: true,
    });

    this.fieldFax = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.fax'),
      placeholder: '',
      required: false,
    });

    this.fieldMobile = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.mobile'),
      placeholder: '',
      required: false,
    });

    this.fieldPhone = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.phone'),
      placeholder: '',
      required: true,
    });

    this.fieldTaxId = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('account-register.taxid'),
      placeholder: '',
      required: this.account.displayTaxId == FieldState.Required,
    });
  };
}
