import { Location } from '@angular/common';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { AuthService, FieldDefinition, MetaDataType, Tab } from 'shared-code-lib';
import {
  Account,
  AccountAddresses,
  AccountRegistrationDetails,
  AddressValidationResult,
  CustomProperty,
} from '../../../blocks/InterFAX/Models/admin';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { ApiResult, Control } from '../../../blocks/services/models';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';

@Component({
  selector: 'app-account-properties',
  templateUrl: './account-properties.component.html',
  styleUrls: ['./account-properties.component.scss'],
})
export class AccountPropertiesComponent implements OnInit {
  @ViewChild('spinnerWrapper', { static: true })
  spinnerWrapper: ElementRef;

  @ViewChild('tabScroll', { static: false })
  tabScroll: PerfectScrollbarDirective;

  activeTab = 'general';
  tabs = new Array<Tab>();

  formControls: Array<Control> = [];
  saveButton: Control;
  cancelButton: Control;

  fieldUsername: FieldDefinition;
  fieldAccountNumber: FieldDefinition;
  fieldAccountType: FieldDefinition;

  account: Account;
  accountRegistrationDetails: AccountRegistrationDetails;

  accountType: string;

  customProperties: Array<CustomProperty>;

  show: boolean;

  addressValidationResults: AddressValidationResult;

  constructor(
    private translateService: TranslateService,
    private router: Router,
    private interfaxService: InterFAXService,
    private toastrService: ToastrService,
    private errorsService: ErrorsService,
    private spinnerService: SpinnerStateService,
    private ngxSmartModalService: NgxSmartModalService,
    private userStateService: UserStateService,
    private authService: AuthService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.createButtons();
    this.createFields();

    this.load();
  }

  private load = () => {
    this.spinnerService.show(this.translateService.instant('Common.LoadingData'), this.spinnerWrapper.nativeElement);
    this.interfaxService.getCustomProperties(this.customPropertiesLoaded, this.loadFailed);
  };

  private customPropertiesLoaded = (customProperties: Array<CustomProperty>): void => {
    this.customProperties = customProperties;
    this.interfaxService.getAccount(this.loaded, this.loadFailed);
  };

  loadFailed = (err: any) => {
    //todo: should show error probably
    this.spinnerService.hide();
  };

  private loaded = (a: Account) => {
    this.account = a;
    this.accountType = this.translateService.instant(
      `fax-account-properties.account-type-${a.accountType.toLocaleLowerCase()}`
    );

    this.interfaxService.getAccountRegistrationDefaults(
      this.account.shippingSameAsBilling ? this.account.billing.country : this.account.shipping.country,
      this.accountRegistrationDetailsLoaded,
      this.loadFailed
    );
  };

  accountRegistrationDetailsLoaded = (ard: AccountRegistrationDetails): void => {
    this.accountRegistrationDetails = ard;
    this.spinnerService.hide();
    this.show = true;
    this.createTabs();
  };

  onSave = () => {
    this.spinnerService.showSavingSpinner();

    if (this.account.billing.changed || this.account.shipping.changed) {
      const addresses = new AccountAddresses();

      if (this.account.billing.changed) addresses.billing = this.account.billing;
      if (!this.account.shippingSameAsBilling && this.account.shipping.changed) {
        addresses.shipping = this.account.shipping;
      }

      this.interfaxService.validateAddresses(addresses, this.addressValidationSuccess, this.addressValidationFailed);
    } else {
      this.saveAccount();
    }
  };

  addressValidationSuccess = (avr: AddressValidationResult): void => {
    if (
      (avr.addresses.billing && !avr.addresses.billing.validated) ||
      (avr.addresses.shipping && !avr.addresses.shipping.validated)
    ) {
      this.errorsService.showErrorsModal(
        [this.translateService.instant('fax-user-properties.enter-valid-address')],
        this.translateService.instant('fax-user-properties.failed-address-validation')
      );
      this.spinnerService.hide();
      return;
    }

    if (avr.billingUpdated || avr.shippingUpdated) {
      this.showAddressValidationModal(avr);
    } else {
      this.saveAccount();
    }
  };

  showAddressValidationModal = (avr: AddressValidationResult): void => {
    this.spinnerService.hide();
    this.addressValidationResults = avr;

    this.ngxSmartModalService.getModal('addressValidationModal').open();
  };

  rejectAddressValidationResults = (): void => {
    this.ngxSmartModalService.getModal('addressValidationModal').close();
  };

  acceptAddressValidationResults = (): void => {
    this.ngxSmartModalService.getModal('addressValidationModal').close();

    if (this.addressValidationResults.billingUpdated) {
      this.account.billing = this.addressValidationResults.addresses.billing;
    }

    if (this.addressValidationResults.shippingUpdated) {
      this.account.shipping = this.addressValidationResults.addresses.shipping;
    }

    this.spinnerService.showSavingSpinner();
    this.saveAccount();
  };

  addressValidationFailed = (e: any): void => {
    this.spinnerService.hide();

    this.errorsService.showErrorsModal(
      [e],
      this.translateService.instant('fax-user-properties.failed-address-validation')
    );
  };

  saveAccount = () => {
    this.account.billing.validated = true;
    if (this.account.shipping) this.account.shipping.validated = true;

    this.interfaxService.setAccount(this.account, false, this.saved, this.saveFailed);
  };

  saved = (result: ApiResult) => {
    if (result.success && result.object?.redirectUrl) {
      this.authService.shareSessionForTabs(60 * 1000);
      return;
    }

    this.spinnerService.hide();
    if (result.success) {
      this.toastrService.success(this.translateService.instant('fax-account-properties.save-success'));
      this.close();
      return;
    }

    this.errorsService.showErrorsModal(result.errors, 'fax-account-properties.save-fail');
  };

  saveFailed = (): void => {
    this.spinnerService.hide();
    this.toastrService.error(this.translateService.instant('fax-account-properties.save-fail'));
  };

  onCancel = () => {
    this.close();
  };

  close = () => {
    this.location.back();
  };

  private createButtons() {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.formControls.push(this.saveButton);
    this.formControls.push(this.cancelButton);

    this.translateService.get('Metadata.Save').subscribe((res) => {
      this.saveButton.setTranslation(res);
    });
    this.translateService.get('Metadata.Cancel').subscribe((res) => {
      this.cancelButton.setTranslation(res);
    });
  }

  createTabs = (): void => {
    this.tabs.push(new Tab('general', this.translateService.instant('fax-account-properties.tab-general')));
    this.tabs.push(new Tab('address', this.translateService.instant('fax-account-properties.tab-address')));

    this.tabs.push(new Tab('retention', this.translateService.instant('fax-account-properties.tab-retention')));
    this.tabs.push(new Tab('permissions', this.translateService.instant('fax-account-properties.tab-permissions')));
    this.tabs.push(
      new Tab('authentication', this.translateService.instant('fax-account-properties.tab-authentication'))
    );
  };

  private createFields = () => {
    this.fieldUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.username'),
      placeholder: '',
      required: false,
    });

    this.fieldAccountNumber = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.account-number'),
      placeholder: '',
      required: false,
    });

    this.fieldAccountType = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.account-type'),
      placeholder: '',
      required: false,
    });
  };

  activateTab = (tab: string): void => {
    this.activeTab = tab;

    if (this.tabScroll) {
      this.tabScroll.scrollToTop();
    }
  };
}
