import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, FixedDataSource, MetaDataType } from 'shared-code-lib';
import { Address } from '../../../../blocks/InterFAX/Models/admin';
import { Countries, Country } from '../../../../blocks/InterFAX/Models/outbound';

@Component({
  selector: 'app-account-address',
  templateUrl: './account-address.component.html',
  styleUrls: ['./account-address.component.scss'],
})
export class AccountAddressComponent implements OnInit {
  @Input() address: Address;
  @Input() countries: Array<Country>;
  @Input() required: boolean;
  @Input() disableCountry: boolean;
  @Output() addressChanged = new EventEmitter();

  fieldAddress1: FieldDefinition;
  fieldAddress2: FieldDefinition;
  fieldCity: FieldDefinition;
  fieldState: FieldDefinition;
  fieldCountry: FieldDefinition;
  fieldPostalCode: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  changed = (): void => {
    this.address.changed = true;
    this.address.validated = false;
    this.addressChanged.emit();
  };

  createFields = (): void => {
    const countryOptions = Countries.getAsFieldOptions(this.countries, this.translateService);
    const countryDataSource = new FixedDataSource(countryOptions);

    this.fieldAddress1 = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.address1'),
      placeholder: '',
      required: this.required,
    });

    this.fieldAddress2 = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.address2'),
      placeholder: '',
      required: false,
    });

    this.fieldCity = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.city'),
      placeholder: '',
      required: this.required,
    });

    this.fieldState = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.state'),
      placeholder: '',
      required: false,
    });

    this.fieldPostalCode = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-account-properties.postal-code'),
      placeholder: '',
      required: this.required,
    });

    this.fieldCountry = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('fax-account-properties.country'),
      placeholder: '',
      required: this.required,
      datasource: countryDataSource,
    });
  };
}
