import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { EnumDataSource, FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import {
  Account,
  CustomProperty,
  DocumentPropertyDataType,
  RetentionPolicyUnit,
} from '../../../../blocks/InterFAX/Models/admin';

@Component({
  selector: 'app-retention-policy',
  templateUrl: './retention-policy.component.html',
  styleUrls: ['./retention-policy.component.scss'],
})
export class RetentionPolicyComponent implements OnInit {
  @Input() account: Account;
  @Input() customProperties: Array<CustomProperty>;

  fieldDuration: FieldDefinition;
  fieldUnit: FieldDefinition;

  fieldCustomProperty: FieldDefinition;
  fieldCustomPropertyDuration: FieldDefinition;
  fieldCustomPropertyUnit: FieldDefinition;

  dateCustomProperties: Array<CustomProperty>;

  get accountRetentionPolicyDuration() {
    return `${this.account.retentionPolicy.duration}`;
  }
  set accountRetentionPolicyDuration(val: string) {
    this.account.retentionPolicy.duration = Number(val);
  }
  get accountRetentionPolicyUnit() {
    return `${this.account.retentionPolicy.unit}`;
  }
  set accountRetentionPolicyUnit(val: string) {
    this.account.retentionPolicy.unit = Number(val);
  }
  get accountRetentionPolicyCustomPropertyDuration() {
    return `${this.account.retentionPolicy.customPropertyDuration}`;
  }
  set accountRetentionPolicyCustomPropertyDuration(val: string) {
    this.account.retentionPolicy.customPropertyDuration = Number(val);
  }
  get accountRetentionPolicyCustomPropertyUnit() {
    return `${this.account.retentionPolicy.customPropertyUnit}`;
  }
  set accountRetentionPolicyCustomPropertyUnit(val: string) {
    this.account.retentionPolicy.customPropertyUnit = Number(val);
  }

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.dateCustomProperties = this.customProperties.filter((cp) => cp.type == DocumentPropertyDataType.Date);
    if (this.dateCustomProperties.length == 0) this.account.retentionPolicy.useCustomProperty = false;

    this.createFields();
  }

  getCustomPropertyDatesDataSource = (): FixedDataSource => {
    const options = new Array<IFieldOptions>();

    for (const cp of this.dateCustomProperties) {
      options.push({ name: cp.name, value: cp.id.toString() });
    }

    return new FixedDataSource(options);
  };

  createFields = (): void => {
    const unitsDataSource = new EnumDataSource(RetentionPolicyUnit, 'RetentionPolicyUnit', this.translateService);

    this.fieldDuration = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('retention-policy.duration'),
      placeholder: '',
      required: true,
      allowNegativeNumbers: false,
    });

    this.fieldUnit = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('retention-policy.units'),
      placeholder: '',
      required: true,
      datasource: unitsDataSource,
    });

    this.fieldCustomProperty = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('retention-policy.custom-property'),
      placeholder: '',
      required: true,
      datasource: this.getCustomPropertyDatesDataSource(),
    });

    this.fieldCustomPropertyDuration = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('retention-policy.duration'),
      placeholder: '',
      required: true,
    });

    this.fieldCustomPropertyUnit = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('retention-policy.units'),
      placeholder: '',
      required: true,
      datasource: unitsDataSource,
    });
  };
}
