import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';

import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar-portable';
import { NgxSmartModalModule } from 'ngx-smart-modal';
import {
  AutoFocusFirstInputModule,
  AutoUpdatePerfectScrollModule,
  CheckboxModule,
  FieldsModule,
  IconsModule,
  LabelModule,
  ModalHeaderModule,
  RadioButtonsGroupModule,
  TabBarModule,
} from 'shared-code-lib';
import { ComponentsModule } from '../../../blocks/components/components.module';
import { ModalsModule } from '../../../blocks/modals/modals.module';
import { PageModule } from '../../../blocks/page/page.module';
import { SAMLOptionsModule } from '../../sso/saml-options/saml-options.module';
import { AccountComponentsModule } from '../account-components/account-components.module';
import { AccountAddressComponent } from './account-address/account-address.component';
import { AccountPropertiesComponent } from './account-properties.component';
import { AddressTabComponent } from './address-tab/address-tab.component';
import { AuthenticationTabComponent } from './authentication-tab/authentication-tab.component';
import { GeneralTabComponent } from './general-tab/general-tab.component';
import { PermissionsTabComponent } from './permissions-tab/permissions-tab.component';
import { RetentionPolicyComponent } from './retention-policy/retention-policy.component';
const routes: Routes = [
  {
    path: '',
    component: AccountPropertiesComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AccountPropertiesRoutingModule {}

@NgModule({
  declarations: [
    AccountPropertiesComponent,
    GeneralTabComponent,
    AccountAddressComponent,
    AddressTabComponent,
    RetentionPolicyComponent,
    PermissionsTabComponent,
    AuthenticationTabComponent,
  ],
  imports: [
    CommonModule,
    TranslateModule,
    PageModule,
    ComponentsModule,
    FormsModule,
    PerfectScrollbarModule,
    IconsModule,
    FieldsModule,
    AutoFocusFirstInputModule,
    TabBarModule,
    CheckboxModule,
    NgxSmartModalModule,
    ModalHeaderModule,
    LabelModule,
    AccountPropertiesRoutingModule,
    RadioButtonsGroupModule,
    AccountComponentsModule,
    ModalsModule,
    AutoUpdatePerfectScrollModule,
    SAMLOptionsModule,
  ],
  exports: [AccountAddressComponent],
})
export class AccountPropertiesModule {}
